* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,
body {
  font-family: "Poppins", sans-serif;
  line-height: 1.6;
  background-color: #eefff3;
}
.display {
  display: flex;
  justify-content: stretch;
  align-items: stretch;
  width: 100%;
  gap: 2%;
}

@media screen and (max-width: 768px) {
  .display {
    display: flex;
    flex-direction: column;
  }
  .sidebar {
    width: 100%;
  }
  .d3 {
    width: 100%;
  }
}
