.sankey {
  width: auto;
  height: 800px;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
    rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
  padding: 20px 40px 40px 40px;
  margin-top: 10px;
  border-radius: 10px;
}
#sankey{
  margin-top: 20px;
}
button {
  width: 100px;
  height: 40px;
}
.legend {
  width: auto;
}
.sankey-legend {
  display: flex;
  gap:35px;
}
.countrytitle {
  display: flex;
  margin-bottom: 10px;
}
.countrytitle img {
  width: 25px;
  height: 25px;
  margin-right: 10px;
}
.path-btns{
  display: flex;
  margin-left:120px;
  justify-content: flex-end;
  width:100%;
}
.path-btns button{
  width:50px;
  height: 40px;
  font-size: 30px;
  color:#FFF;
  background-color: #32A35A;
  margin:0px 20px;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  cursor: pointer;
  outline: none;
  border: #32A35A;
  line-height: 40px;
}

@media screen and (max-width:768px) {
  .sankey-legend {
    display: flex;
    gap:0px;
  }
  .sankey {
    height: 600px;
  }
}
