nav {
  width: 70px;
  height: 800px;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  transition: all 0.2s ease-in;
  position: relative;
}
nav ul {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  list-style: none;
  padding-top: 90px;
  margin: 0px;
}
nav ul li {
  margin: 5px 0px;
  color: #32a35a;
  font-size: 16px;
  font-weight: 500;
  width: 100%;
  height: 50px;
  padding: 10px 25px;
  transition: all 0.5s linear;
  display: flex;
}
nav ul img {
  margin-right: 10px;
  vertical-align: middle;
  width: 25px;
  height: 25px;
}
nav ul li:hover {
  background-color: #32a35a;
  color: #fff;
  cursor: pointer;
}
nav:hover {
  width: 230px;
}
@media screen and (max-width: 768px) {
  nav {
    width: 100%;
    height: auto;
  }
  nav ul {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding-top: 0px;
  }
  nav:hover{
    width:100vw;
  }
  nav ul li span{
    display: none;
  }
}
