.footer {
  width: 100%;
  height: 60px;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  position: fixed;
  bottom: 0;
}
.slider {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  margin-top:5px;
}
.div-btn{
  padding:0px 20px 20px  0px;
}
.div-btn button:focus{
  background-color: black;
  color: red;
}
@media screen and (max-width:768px) {
  .slider{
    margin-right: 30px;
  }
}