.color-input {
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  padding: 10px;
  margin-top: 10px;
  border-radius: 5px;
}
.inputs {
  width: 150px;
  height: auto;
  display: flex;
  overflow-y: auto;
}
.inputs input {
  width: 40px;
  height: 20px;
  border: none;
  outline: none;
  background: none;
}
.colorspan {
  font-size: 12px;
  text-align: left;
  width: 350px;
  padding-left: 10px;
}
