.country {
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  padding: 10px;
  border-radius: 5px;
  position: absolute;
  left: 230px;
  width: 130px;
}
.space-country {
  display: flex;
  height: 30px;
  align-items: center;
  position: relative;
}
.country .logo img {
  width: 20px;
  height: 20px;
  margin-right: 10px;
  vertical-align: middle;
}
.logo-text span {
  font-size: 12px;
  font-weight: 500;
}
.emission-balance {
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  position: absolute;
  left: 130px;
  top: 0px;
  display: none;
  list-style: none;
}
.emission-balance span {
  width: 150px;
  height: 40px;
  padding: 10px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
}
.emission-balance span:hover {
  background-color: #00792c;
  color: #fff;
}
.space-country:hover > .emission-balance {
  left: 100px;
  display: flex;
  flex-direction: column;
}
