.header{
    width:100%;
    height: 60px;
    background-color: #32A35A;
}
.container{
    max-width: 1150px;
    margin:0 auto;
    padding:10px;
    overflow: hidden;
    
}
.header .container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}
.logo {
    display: flex;
    align-items: center;
    height: 100%;
}

.logo h2{
    color:#FFF;
    font-size: 20px;
    font-weight: 600;
}
.logo img {
    width:40px;
    height: 40px;
    margin-right:20px;
}
.profile {
    display: flex;
    align-items: center;
    height: 100%;
}
.profile span:first-child{
    color:#FFf;
    font-weight: 300;
    font-size: 16px;
    margin-right: 0px;
}
.profile .name{
    color:#FFf;
    font-weight: 500;
    font-size: 16px;
    margin-left:10px;
}
.profile img {
    width:40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-left:10px;
}
.dropdown{
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    border-radius: 5px;
    position: absolute;
    right:10px;
    width: 150px;
}
.profileData,.logoutData{
    transition: all 0.5s ease-in;
    cursor: pointer;
    padding: 10px;
    height: 40px;
    color:#00792C;
    line-height: 20px;
}
.profileData img {
    vertical-align: middle;
    width:20px;
    height:20px;
}
.profileData span{
    font-size: 14px;
    font-weight: 500;
    margin-left: 10px;
}
.logoutData img {
    vertical-align: middle;
    width:20px;
    height:20px;
}
.logoutData span{
    font-size: 14px;
    font-weight: 500;
    margin-left: 10px;
    color:inherit;
}
.profileData:hover,.logoutData:hover{
   background-color: #00792C;
   color:#FFF;
}
@media screen and (max-width:768px){
    .header{
        height: auto;
    }
    .header .container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100%;
    }
    .logo h2 {
        font-size: 20px;
    }
    .dropdown{
        z-index: 1;
        right: 50px;
    }

}